import React, {PureComponent, Suspense} from "react";
import {Layout, Menu, Spin} from "antd";
import {connect} from "react-redux";
import {Link, Route, Switch} from "react-router-dom";
import {Wrapper} from './style';
import AuthComponent from "../common/authComponent ";
const {Sider} = Layout;
const {Content} = Layout;
const AuthMenuItem = AuthComponent(Menu.SubMenu);
//左侧菜单不含子集的路由
const renderMenuItem = item => {
    return (<Menu.Item key={item.key}>
            <Link to={{ pathname:item.key, query : {stage:item.stage}}}>
                {item.icon && <i className={item.icon}/>}
                <span>{item.title}</span>
            </Link>
        </Menu.Item>
    );
}


//左侧菜单包含子集的路由
const renderSubMenu = (item,authCodes) => (
    <AuthMenuItem source="route" authcode={item.id} authcodes = {authCodes.menuAuthCode}
        key={item.key}
        title={
            <span>
        {item.icon && <i className={item.icon}/>}
                <span>{item.title}</span>
      </span>
        }
    >
        {item.subs.map(item => renderMenuItem(item))}
    </AuthMenuItem>
);
//右侧内容区域路由信息
const renderRoute = menus => {
    const routes = menus.reduce((pre, next) => {
        return next.subs ? [...pre, ...next.subs] : [...pre, next]
    }, [])
    return routes.map(route =>
        (
            <Route
                key={route.key}
                path={route.key}
                exact
                render={props => (
                    <route.component {...props} />
                )}/>

        )
    )
};
//左侧菜单
const renderSider = (siderMenus,authCodes,eve) => {
    let activeIndex = 0;
    let currentMenuList = [];
    if(siderMenus.length !== 0){
        authCodes.menuAuthCode.map((val,key)=>{
            siderMenus.map((menuVal,menuKey)=>{
                if(val.indexOf(menuVal.id)!=-1){
                    currentMenuList.push(menuVal.id);
                    activeIndex = menuKey;
                }
            })
        })
    }
    /* if(currentMenuList.length<siderMenus.length){
        // console.log(event,'this.props1246798')
        if(event.type!=="click"){
            window.location.href = `${window.location.href.split("#")[0]}#${siderMenus[activeIndex].subs[0].key}`
        }
    } */
    
    return siderMenus!=undefined  ? (<Sider>
        <Menu
            mode="inline"
            defaultSelectedKeys={[siderMenus[0].subs ? (siderMenus.length !== 0 && currentMenuList.length<siderMenus.length?siderMenus[activeIndex].subs[0].key:siderMenus[0].subs[0].key) : siderMenus[0].key]}
            defaultOpenKeys={[(siderMenus.length !== 0 && currentMenuList.length<siderMenus.length?siderMenus[activeIndex].key:siderMenus[0].key)]}
            style={{height: "100%"}}
        >
            {
                siderMenus.map(siderMenu =>
                    siderMenu.subs
                        ? renderSubMenu(siderMenu,authCodes)
                        : renderMenuItem(siderMenu)
                )}
        </Menu>
    </Sider>) : null
}


class Panel extends PureComponent {
    render() {
        const {siderMenus,authCode} = this.props;
        const authCodes = authCode.toJS();
        return (
            <Wrapper>
                <Layout className="layout-inside">
                    {renderSider(siderMenus,authCodes)}
                    <Layout>
                        <Content className="layout-content">
                            <Suspense fallback={<Spin/>}>
                                <Switch>
                                    {
                                        renderRoute(siderMenus)
                                    }
                                </Switch>
                            </Suspense>
                        </Content>
                    </Layout>
                </Layout>
            </Wrapper>
        )
    }
    componentDidMount(){
        // console.log(this.props.match.params);
    }
}

const mapStateToProps = state => {
    return {
        siderMenus: state.getIn(["panel", "siderMenus"]),
        authCode: state.getIn(["login", "authCode"])
    };
};
export default connect(
    mapStateToProps,
    null
)(Panel);
