import styled from 'styled-components';
import loginLogo from '../../static/imgs/icon.png';

export const Wrapper = styled.div`
    padding: 20px;
    height: calc(100vh - 80px);
    .layout-inside {
        height: 100%;
        .ant-layout-sider {
            height: 100%;
            width: 250px !important;
            flex: 0 0 250px !important; 
            max-width: 250px !important;
            min-width: 250px !important;
            background-color:#fff;
            .ant-menu-inline, .ant-menu-vertical, .ant-menu-vertical-left {
                border-right: 1px solid #eee;
            }
            
            .ant-menu-submenu-title {
                margin: 0;
                border-bottom: 1px solid #eee;
                height: 60px;
                line-height: 60px;
            }
            .all-progress,.section-progress,.pedestal,.video-surveillance,.schedule-setting,.platform-setting{
                width: 30px;
                height: 25px;
                display: inline-block;
                margin-right: 10px;
                vertical-align: middle;
            }
            
            .seat-status ,.binding-list ,.making-list{
                width: 20px;
                height: 18px;
                display: inline-block;
                margin-right: 5px;
                vertical-align: middle;
            }
            
            .platform-setting {
                height: 29px;
                background: url(${loginLogo}) no-repeat -174px -239px;
            }
            .schedule-setting {
                height: 29px;
                background: url(${loginLogo}) no-repeat -140px -239px;
            }
            .video-surveillance{
                background: url(${loginLogo}) no-repeat 0 -239px;
            }
            .all-progress {
                background: url(${loginLogo}) no-repeat -69px -240px;
            }
            .section-progress {
                background: url(${loginLogo}) no-repeat -106px -240px;
            }
            .pedestal {
                background: url(${loginLogo}) no-repeat -212px -240px;
            }
            .seat-status {
                background: url(${loginLogo}) no-repeat -250px -246px;
            }
            
            .binding-list {
                background: url(${loginLogo}) no-repeat -278px -245px;
            }
            
            .making-list {
                margin-right: 8px;
                background: url(${loginLogo}) no-repeat -302px -246px;
            }
            .ant-menu-item {
                height: 60px;
                line-height: 60px;
                color: #5a5a5a;
                margin: 0;
                border-bottom: 1px solid #eee;
                &:hover{
                    background-color:#f0f8ff;
                    font-weight: 700;
                }
                &.ant-menu-item-selected{
                    font-weight: 700;
                }
            }
        
        }
        .layout-content {
            background: #fff;
            padding: 0 30px;
            .ant-select-selection.ant-select-selection--single {
                height: 36px;
                line-height: 36px;
                .ant-select-selection__rendered {
                    height: 36px;
                }
            }
        }
    }
`;
